* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ebebeb;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 40px;
}

.flag {
  display: block;
  width: 60px;
  height: 40px;
  margin-right: 10px;
}
.country-list__item {
  width: 350px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px 5px 5px 15px;
}

.country-info {
  display: block;
  width: 350px;
  padding: 20px;
  border-radius: 20px;

  background-color: #dfdfdf;
}
.card-flag {
  margin: 0 auto;
}
.country__name {
  font-size: 18px;
  text-align: center;
}

.country__card {
  list-style: none;
  padding-left: 25px;
  font-size: 16px;
}
